.wpforms-form .wpforms-field-container {
  display: flex;
  align-items: center;
  column-gap: 10px;
  row-gap: 20px;
}
.wpforms-form .wpforms-field-container select,
.wpforms-form .wpforms-field-container option,
.wpforms-form .wpforms-field-container input,
.wpforms-form .wpforms-field-container textarea {
  max-width: 100%;
  padding: 15px;
  background-color: transparent;
  font-family: var(--montserrat);
  font-size: 2rem;
  color: rgb(var(--text-color));
  border-bottom: 1px solid rgb(var(--text-color));
  border-radius: 0;
}
@media (min-width: 768px) {
  .wpforms-form .wpforms-field-container select,
  .wpforms-form .wpforms-field-container option,
  .wpforms-form .wpforms-field-container input,
  .wpforms-form .wpforms-field-container textarea {
    font-size: 1.6rem;
    line-height: 3rem;
  }
}
.wpforms-form .wpforms-field-container select::placeholder,
.wpforms-form .wpforms-field-container option::placeholder,
.wpforms-form .wpforms-field-container input::placeholder,
.wpforms-form .wpforms-field-container textarea::placeholder {
  color: rgb(var(--text-color));
}
.wpforms-form .wpforms-field-container .wpforms-field-label {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
}
.wpforms-form .wpforms-field-container .wpforms-field {
  max-width: 100%;
  width: 100%;
  padding: 0 0;
  background-color: transparent;
}
.wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul {
  margin: 10px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li {
  width: fit-content;
  height: fit-content;
  padding: 5px 10px;
  position: relative;
  border: 1.5px solid rgb(var(--light-grey));
  border-radius: 25px;
  cursor: pointer;
}
.wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li:hover, .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li.wpforms-selected, .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li:has(input:checked) {
  background-color: rgb(var(--primary-color));
}
.wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li input {
  width: 100%;
  height: 100%;
  margin: 0;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 10;
  opacity: 0;
  cursor: pointer;
}
.wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li label {
  position: relative;
  z-index: 0;
  font-size: 1.6rem;
  color: rgb(var(--light-grey));
}
.wpforms-form .wpforms-submit-container {
  margin-top: 20px;
}
.wpforms-form .wpforms-submit-container [type=submit] {
  appearance: none;
  padding: 16px 28px;
  background-color: rgb(var(--secondary-color));
  border: none;
  text-transform: uppercase;
  font-family: var(--montserrat);
  font-weight: normal;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
}
.wpforms-form .wpforms-submit-container [type=submit]:hover {
  background-color: rgb(var(--tertiary-color));
}

:root {
  --adore: "Adore Condensed", Times, serif;
  --century: "Century Gothic", arial, sans-serif;
  --montserrat: "Montserrat", arial, sans-serif;
}

* {
  margin: 0;
  padding: 0;
  border: none;
}

html {
  width: 100%;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  max-width: 100vw;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  line-height: 3.4rem;
  font-family: var(--montserrat);
}
@media (min-width: 768px) {
  body {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

section.block {
  max-width: 100vw;
  content-visibility: auto;
  transition: 0.3s ease;
}
section.block[data-visibility=hidden] {
  background-color: #000;
}
section.block[data-visibility=hidden] > * {
  visibility: hidden;
  opacity: 0;
}
section.block[data-visibility=visible] > * {
  visibility: visible;
  opacity: 1;
}

/*Headings*/
h1,
h2,
h3,
h4,
h5 {
  font-family: var(--adore);
  color: rgba(var(--tertiary-color));
}

h1,
.h1,
h1 *,
.h1 * {
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 5.8rem;
  letter-spacing: 0.5px;
  font-family: var(--adore);
}
@media (min-width: 768px) {
  h1,
  .h1,
  h1 *,
  .h1 * {
    font-size: 4rem;
    line-height: 7rem;
  }
}
@media (min-width: 1441px) {
  h1,
  .h1,
  h1 *,
  .h1 * {
    font-size: 7.8rem;
    line-height: 10rem;
  }
}

h2,
.h2 {
  font-size: 3rem;
  font-weight: 600;
  font-family: var(--adore);
  line-height: 4.5rem;
}
@media (min-width: 768px) {
  h2,
  .h2 {
    font-size: 4rem;
    line-height: 5rem;
  }
}
@media (min-width: 1441px) {
  h2,
  .h2 {
    font-size: 5rem;
    line-height: 6.5rem;
  }
}

h3,
.h3,
.h3 *,
h3 * {
  font-family: var(--montserrat);
  font-size: 2.4rem;
  font-weight: 600;
  line-height: normal;
}

h4,
.h4,
.h4 *,
h4 * {
  font-family: var(--montserrat);
  font-size: 2.4rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  line-height: normal;
}
@media (min-width: 1441px) {
  h4,
  .h4,
  .h4 *,
  h4 * {
    font-size: 3.2rem;
    line-height: 4.6rem;
  }
}

h5,
.h5,
.h5 *,
h5 * {
  font-family: var(--montserrat);
  font-size: 1.8rem;
  line-height: 3.2rem;
  font-weight: 600;
}
@media (min-width: 768px) {
  h5,
  .h5,
  .h5 *,
  h5 * {
    font-size: 2.5rem;
    line-height: 3.5rem;
  }
}

h6,
.h6,
.h6 *,
h6 * {
  font-family: var(--montserrat);
  font-size: 2.3rem;
  line-height: 2.6rem;
  font-weight: 600;
}

/*Globales*/
a {
  font-family: var(--montserrat);
  color: rgb(var(--secondary-color));
  font-size: 2rem;
  line-height: 3.4rem;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
}
@media (min-width: 768px) {
  a {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

a:hover,
a:active,
a:focus {
  color: rgb(var(--primary-color));
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li a {
  width: 100%;
  height: 100%;
  display: block;
}

p {
  font-family: var(--montserrat);
  color: rgb(var(--text-color));
  font-size: 2rem;
  line-height: 3.4rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

picture {
  display: flex;
}

img {
  max-width: 100%;
  height: auto;
  transition: opacity 0.3s;
}

.txt-center {
  text-align: center;
}

.container {
  max-width: 1230px;
  width: 90%;
  margin: 0 auto;
}
.container.page {
  padding: 8rem 0;
}

.formatted-text {
  font-size: 2rem;
  font-family: var(--montserrat);
  font-weight: 400;
  color: rgba(var(--text-color), 1);
  line-height: 3.4rem;
}
@media (min-width: 768px) {
  .formatted-text {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}
.formatted-text h1,
.formatted-text h2,
.formatted-text h3,
.formatted-text h4,
.formatted-text h5,
.formatted-text h6 {
  margin-bottom: 20px;
}
.formatted-text p,
.formatted-text span,
.formatted-text li {
  margin-bottom: 1.6rem;
  color: rgba(var(--text-color), 1);
  font-size: 2rem;
  font-family: var(--montserrat);
  font-weight: 400;
  line-height: 3.4rem;
  vertical-align: text-top;
}
@media (min-width: 768px) {
  .formatted-text p,
  .formatted-text span,
  .formatted-text li {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

.section-title {
  font-size: 2.6rem;
  font-family: var(--adore);
  font-weight: bold;
  color: rgb(var(--primary-complement));
  letter-spacing: 2px;
  line-height: 3.2rem;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 3rem;
    line-height: 3.4rem;
  }
}
@media (min-width: 1024px) {
  .section-title {
    font-size: 4rem;
    line-height: 4.4rem;
  }
}

[type=submit],
.btn {
  min-width: fit-content;
  padding: 7.5px 15px;
  display: inline-block;
  transition: 0.3s ease-in-out;
}
[type=submit] > *,
.btn > * {
  font-family: var(--adore);
  font-size: 2rem;
  line-height: 3.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  [type=submit] > *,
  .btn > * {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}
[type=submit].white,
.btn.white {
  background-color: #fff;
  color: rgb(var(--text-color));
}
[type=submit].white:hover,
.btn.white:hover {
  background-color: rgb(var(--primary-color), 1);
  color: #fff;
}
[type=submit].primary,
.btn.primary {
  background-color: rgb(var(--primary-color));
  color: #fff;
}
[type=submit].primary:hover,
.btn.primary:hover {
  background-color: #fff;
  color: #000;
}
[type=submit].secondary,
.btn.secondary {
  background-color: rgb(var(--secondary-color));
  color: #fff;
}
[type=submit].secondary:hover,
.btn.secondary:hover {
  background-color: rgb(var(--primary-color), 1);
  color: #fff;
}
[type=submit].arrow,
.btn.arrow {
  padding: 20px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}
[type=submit].arrow::after,
.btn.arrow::after {
  content: "â†’";
  margin-left: 1rem;
  display: inline-block;
  font-size: 2.5rem;
}

.post-pagination {
  width: 100%;
  flex: 0 0 100%;
  grid-column: 1/4;
  display: flex;
  gap: 15px;
  justify-content: center;
  align-items: center;
}
.post-pagination a,
.post-pagination span {
  padding: 1rem;
  font-size: 2rem;
  line-height: 2.4rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .post-pagination a,
  .post-pagination span {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

/* Properly align images embeded in content */
img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.wp-block-image:has(.alignleft) {
  float: left;
  display: flex;
  width: fit-content;
  contain: content;
}

.wp-block-image:has(.alignleft) img {
  height: auto;
}

.wp-block-image:has(.alignleft) .alignleft {
  display: flex;
  width: fit-content;
  contain: content;
}

@media (max-width: 500px) {
  .wp-block-image:has(.alignleft) {
    width: 100%;
    height: auto;
    margin: 0px 0px 20px 0px !important;
  }
  .wp-block-image:has(.alignleft):not(:has(svg)) {
    float: none;
  }
  .wp-block-image:has(.alignleft):has(svg) {
    width: auto;
  }
  .wp-block-image:has(.alignleft):has(svg) .alignleft,
  .wp-block-image:has(.alignleft):has(svg) figure,
  .wp-block-image:has(.alignleft):has(svg) svg {
    width: auto;
  }
  .wp-block-image:has(.alignleft) .alignleft {
    width: 100%;
  }
  .wp-block-image:has(.alignleft) img {
    width: 100%;
    height: auto;
  }
}
.alignleft:not(:only-child) {
  float: left;
  display: block;
  margin: 10px 20px 0px 0px;
  max-width: 100%;
}

.alignleft:not(:only-child) img {
  width: auto;
  height: auto;
}

@media (max-width: 500px) {
  .alignleft:not(:only-child) {
    float: none;
    width: 100%;
    height: auto;
    margin: 0px 0px 20px 0px !important;
  }
  .alignleft:not(:only-child) img {
    width: 100%;
    height: auto;
  }
}
p:has(.alignleft) {
  max-width: 100%;
  width: fit-content;
  margin: 0;
  margin-right: 20px;
  display: flex;
  float: left;
}

@media (max-width: 500px) {
  p:has(.alignleft) {
    width: 100%;
  }
}
p:has(.alignleft) img.alignleft {
  margin: 0;
  margin-right: 0;
}

img.alignleft:not(:only-child) {
  margin: 10px 20px 0px 0px;
  float: left;
}

img.alignleft:not(:only-child) img {
  width: auto;
  height: auto;
}

@media (max-width: 500px) {
  img.alignleft:not(:only-child) {
    float: none;
    width: 100%;
    height: auto;
    margin: 0px 0px 20px 0px !important;
  }
  img.alignleft:not(:only-child) img {
    width: 100%;
    height: auto;
  }
}
.aligncenter {
  max-width: 100%;
}

.aligncenter img {
  width: 100%;
  height: auto;
  display: block;
}

img.aligncenter {
  display: block;
  max-width: 100%;
  height: auto;
}

iframe {
  max-width: 100%;
  width: 100%;
}

@media (max-width: 767px) {
  .hide-on-mobile {
    display: none !important;
  }
}
@media (max-width: 1023px) {
  .hide-on-tablet {
    display: none !important;
  }
}
@media (min-width: 1024px) {
  .hide-on-desktop {
    display: none !important;
  }
}
.site-header {
  width: 100%;
  height: auto;
  padding: 0.5rem 2.4rem;
  position: relative;
  top: 0;
  left: 0;
  z-index: 99;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.site-header.static {
  position: relative;
}
.site-header.sticky {
  position: fixed;
}
.site-header.static .site-header, .site-header.scrolling .site-header {
  background-color: #fff;
}
.site-header.static #logo img, .site-header.scrolling #logo img {
  filter: invert(0);
}
.site-header.static #menu-mobile hr, .site-header.scrolling #menu-mobile hr {
  background-color: #000;
}
.site-header.active .site-header__container {
  height: 100vh;
  transition: 0.8s ease-in-out;
}
@media (min-width: 1024px) {
  .site-header.active .site-header__container {
    height: 100%;
  }
}
.site-header.active #main-nav-section .main-menu .menu > .menu-item > a {
  opacity: 1;
  transition: opacity 0.5s ease-in-out calc(0.1s * (var(--length) + var(--index) + 1));
}
.site-header.active #logo,
.site-header.active #menu-mobile {
  position: relative;
  z-index: 4;
}
.site-header.active #logo img,
.site-header.active #menu-mobile img {
  filter: invert(0);
}
.site-header.active #logo hr,
.site-header.active #menu-mobile hr {
  background-color: #000;
}
.site-header.active #logo hr:first-child,
.site-header.active #menu-mobile hr:first-child {
  position: absolute;
  top: 50%;
  left: 0;
  transform: rotate(45deg);
}
.site-header.active #logo hr:nth-child(2),
.site-header.active #menu-mobile hr:nth-child(2) {
  opacity: 0;
  transition: none;
}
.site-header.active #logo hr:last-child,
.site-header.active #menu-mobile hr:last-child {
  position: absolute;
  top: 50%;
  right: 0;
  transform: rotate(-45deg);
}
.site-header__container {
  width: 100%;
  height: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  background-color: #fff;
  display: flex;
  justify-content: center;
  transition: 0.8s ease-in-out 0.4s;
}
.site-header #logo {
  width: 90px;
  height: 90px;
  position: relative;
}
.site-header #logo img {
  width: 100%;
  height: 100%;
  position: relative;
  display: block;
  object-fit: contain;
  object-position: center center;
  filter: invert(100%);
  transition: filter 1s ease 0.4s, opacity 0.5s ease-in-out;
}
.site-header #logo img.logo {
  z-index: 1;
  opacity: 1;
}
.site-header #logo img.imago {
  height: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
  opacity: 0;
}
.site-header #logo:hover .logo {
  opacity: 0;
}
.site-header #logo:hover .imago {
  opacity: 1;
}
.site-header #menu-mobile {
  width: 30px;
  height: 20px;
  padding: 0;
  position: relative;
  align-self: center;
  justify-self: end;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 2px;
  cursor: pointer;
}
.site-header #menu-mobile hr {
  width: 100%;
  height: 2px;
  border-radius: 1px;
  background-color: #fff;
  transition: 1s ease 0.4s;
}
.site-header #main-nav-section {
  width: 100%;
  height: auto;
}
.site-header #main-nav-section .main-menu {
  height: calc(100% - 90px);
  width: calc(100% - 120px);
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .site-header #main-nav-section .main-menu {
    height: 100%;
    width: 100%;
  }
}
.site-header #main-nav-section .main-menu .menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  gap: 10px;
}
@media (min-width: 1024px) {
  .site-header #main-nav-section .main-menu .menu {
    position: relative;
    z-index: 0;
    flex-direction: row;
    gap: 18px;
  }
}
.site-header #main-nav-section .main-menu .menu > .menu-item > a {
  opacity: 0;
  transition: opacity 0.5s ease-in-out calc(0.1s * (var(--length) - (var(--index) + 1)));
}
.site-header #main-nav-section .main-menu .menu li {
  text-align: left;
}
.site-header #main-nav-section .main-menu .menu li a {
  font-family: var(--montserrat);
  color: #000;
}

.site-footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(var(--tertiary-color), 1);
}
.site-footer__inner {
  max-width: 1800px;
  width: 95%;
  padding: 50px 0;
  margin: 0 auto;
  display: flex;
}
@media (min-width: 1024px) {
  .site-footer__inner {
    padding: 120px 0;
  }
}
.site-footer__copy {
  padding: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.site-footer__copy p,
.site-footer__copy span,
.site-footer__copy a {
  color: rgba(var(--primary-color), 1);
  text-align: center;
}
.site-footer .cols {
  width: 100%;
  height: 100%;
  padding: 0 50px;
  display: flex;
  flex-wrap: wrap;
  column-gap: 20rem;
  row-gap: 3rem;
}
.site-footer .cols .col {
  min-width: 280px;
  display: flex;
  align-self: flex-start;
  justify-content: center;
  flex: 1 0 calc(60% - 10rem);
}
.site-footer .cols .col .title {
  font-family: var(--adore);
  font-size: 8.5rem;
  font-weight: 700;
  line-height: 1.5;
  text-transform: uppercase;
  color: rgba(var(--secondary-color), 1);
}
@media (max-width: 1023px) {
  .site-footer .cols .col .title {
    font-size: 3.6rem;
  }
}
.site-footer .cols .col .name {
  margin-bottom: 15px;
  font-weight: bold;
}
.site-footer .cols .col p,
.site-footer .cols .col a,
.site-footer .cols .col span {
  font-size: 1.6rem;
  color: rgba(var(--secondary-color), 1);
}
.site-footer .cols .col a {
  text-decoration: underline;
}
.site-footer .cols .col a:hover {
  color: rgba(var(--light-grey), 1);
}
.site-footer .cols .col:last-child {
  flex: calc(40% - 10rem);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 3rem;
}
.site-footer .cols .col:last-child .menus,
.site-footer .cols .col:last-child .locations {
  width: 100%;
  flex-direction: column;
  gap: 2rem;
}
.site-footer .cols .col:last-child .menus .location,
.site-footer .cols .col:last-child .locations .location {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.cover {
  width: 100%;
  min-height: fit-content;
  height: 100vh;
  position: relative;
  display: flex;
  background-color: #000;
}
.cover__bg {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: flex;
  opacity: 0;
  transition: 0.4s ease;
}
.cover__bg video,
.cover__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  display: block;
  transition: 0.4s ease;
}
.cover__wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}
.cover__inner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .cover__inner {
    justify-content: flex-end;
    align-items: flex-end;
  }
}
.cover .title {
  text-align: center;
  font-family: var(--adore);
  font-size: 3.8rem;
  line-height: 1.2;
  letter-spacing: 1.8rem;
  color: #fff;
}
@media (min-width: 768px) {
  .cover .title {
    font-size: 6.2rem;
    letter-spacing: 2.8rem;
  }
}
@media (min-width: 1024px) {
  .cover .title {
    font-size: 10rem;
    letter-spacing: 5rem;
  }
}

.cover-inner {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -1;
  display: flex;
  background-color: #000;
  overflow: hidden;
}
.cover-inner__bg {
  width: 100%;
  height: 100%;
  display: flex;
  opacity: 0;
  transition: opacity 0.4s ease;
}
.cover-inner__bg img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
  transition: opacity 0.4s ease;
}
.cover-inner__content {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cover-inner .title {
  text-align: center;
  font-family: var(--adore);
  font-size: 3.8rem;
  line-height: 1.2;
  letter-spacing: 1.8rem;
  color: #fff;
}
@media (min-width: 768px) {
  .cover-inner .title {
    font-size: 6.2rem;
    letter-spacing: 2.8rem;
  }
}
@media (min-width: 1024px) {
  .cover-inner .title {
    font-size: 10rem;
    letter-spacing: 5rem;
  }
}
.cover-inner + * {
  margin-top: 100vh;
}